*{
  box-sizing: border-box;
  margin:0;
  padding:0;
}
body {
  margin: 0;
  font-family: 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
p{
  font-size: 1.6rem;
}
h1{
  font-size: 5rem;
}
.app{
  width:100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0, 0, 0, 0.3);
  color:#ffff;
}
.app:before{
  content: '';
  background: url('./assets/sunset.jpg') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top:0;
  left:0;
  z-index:-1;
}
.container{
  max-width: 700px;
  height: 600px;
  margin:auto;
  padding: 0 1rem;
  position: relative;
  top:10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.app .search{
  text-align:center;
  padding: 1 rem;

}
.app input{
    margin-top: 30px;
    padding: .7rem 1.5rem;
    font-size: 1.2rem;
    border-radius:25px;
    border:1px solid rgba(255,255,255,0.8); 
    background: rgba(255,255,255,0.1);
    color:#f8f8f8;


}
::placeholder{
  color:#f8f8f8;
}
.app .top{
  width:100%;
  margin:1rem auto;
}
.app .description{
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(270deg);
}
.app .bottom{
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 10px;
  background-color: rgba(255,255,255,0.1);
   
}
.bold{
  font-weight: 700;
}
.location {
  font-size:xx-large;
  font-weight: 100;
}
.temp .p{
  font-size: 5rem;
  font-weight: 300;
}